<script>
  import { onMount } from 'svelte';
  import Chart from 'chart.js/auto';

  let map;
  let weatherData = [];
  let isLoading = false;
  let errorMessage = '';
  let searchInput = '';
  let currentAddress = '';
  let currentMarker = null;
  let searchResults = []; //     
  let weatherChart; //    

  async function fetchWeatherData(lat, lon) {
    const API_KEY = '1e657dc0d8ee9f105075aab7719defa8';
    const units = 'metric';
    const url = `https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${lon}&units=${units}&appid=${API_KEY}`;

    isLoading = true;
    try {
      const response = await fetch(url);
      if (!response.ok) throw new Error('API  : ' + response.statusText);

      const data = await response.json();
      weatherData = data.list.map((forecast) => ({
        date: new Date(forecast.dt * 1000).toLocaleString(),
        temperature: forecast.main.temp,
        humidity: forecast.main.humidity,
        windSpeed: forecast.wind.speed,
        clouds: forecast.clouds.all,
      }));
      updateWeatherChart();
    } catch (error) {
      console.error('     :', error);
      errorMessage = '     : ' + error.message;
    } finally {
      isLoading = false;
    }
  }

  async function loadNaverMapsAPI() {
    return new Promise((resolve, reject) => {
      const script = document.createElement('script');
      script.src = 'https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=tbl2cislwz&submodules=geocoder';
      script.async = true;
      script.onload = () => resolve();
      script.onerror = () => reject(new Error('   '));
      document.head.appendChild(script);
    });
  }

  onMount(async () => {
    await loadNaverMapsAPI();

    const mapOptions = {
      center: new naver.maps.LatLng(37.5665, 126.978),
      zoom: 10,
    };
    map = new naver.maps.Map('map', mapOptions);

    naver.maps.Event.addListener(map, 'click', async function (e) {
      const lat = e.coord.lat();
      const lon = e.coord.lng();

      //     .
      const navermaps = window.naver.maps;
      await navermaps.Service.reverseGeocode(
        {
          location: new navermaps.LatLng(lat, lon),
        },
        function (status, response) {
          if (status !== navermaps.Service.Status.OK) {
            console.log('Something went wrong!', status);
            return;
          }

          const result = response.result;
          currentAddress = result.items[0].address; //     .
          addMarker(lat, lon);
          fetchWeatherData(lat, lon);
        }
      );
    });
  });

  //       searchResults 
  async function searchAddress() {
    if (!searchInput.trim()) return;
    isLoading = true;
    errorMessage = '';
    searchResults = []; //    

    const navermaps = window.naver.maps;

    try {
      const response = await new Promise((resolve, reject) => {
        navermaps.Service.geocode({ query: searchInput }, function (status, response) {
          if (status === navermaps.Service.Status.ERROR) {
            reject(new Error('    .'));
          } else {
            resolve(response);
          }
        });
      });

      //     10 searchResults  
      searchResults = response.v2.addresses.slice(0, 10).map((address) => ({
        label: address.roadAddress || address.jibunAddress, //     
        x: address.x,
        y: address.y,
      }));
    } catch (error) {
      errorMessage = error.message;
    } finally {
      isLoading = false;
    }
  }

  //   
  function addMarker(lat, lon) {
    //        .
    if (currentMarker) {
      currentMarker.setMap(null); //    
    }

    //     URL
    const markerImageUrl = './marker.png'; //   URL   .

    //   
    currentMarker = new naver.maps.Marker({
      position: new naver.maps.LatLng(lat, lon),
      map: map,
      icon: {
        content: `<img src="${markerImageUrl}" alt="Marker" style="width: 30px; height: 87.7px;">`, //    
        anchor: new naver.maps.Point(15, 43.85), //   .             
      },
    });

    //      .
    fetchWeatherData(lat, lon); //    
  }

  //        
  function selectAddress(address) {
    currentAddress = address.label;
    map.setCenter(new naver.maps.LatLng(address.y, address.x));
    addMarker(address.y, address.x);
    fetchWeatherData(address.y, address.x);
    searchResults = []; //   
  }

  function updateWeatherChart() {
    const ctx = document.getElementById('weatherChart').getContext('2d');
    if (weatherChart) {
      weatherChart.data.labels = weatherData.map((data) => data.date.slice(0, 10));
      weatherChart.data.datasets[0].data = weatherData.map((data) => data.temperature);
      weatherChart.update();
    } else {
      weatherChart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: weatherData.map((data) => data.date.slice(0, 10)),
          datasets: [
            {
              label: ' (C)',
              data: weatherData.map((data) => data.temperature),
              borderColor: 'rgb(75, 192, 192)',
              tension: 0.1,
            },
          ],
        },
        options: {
          scales: {
            y: {
              beginAtZero: false,
            },
          },
        },
      });
    }
  }
</script>

<main>
  <div id="map" style="width: 100%; height: 400px;"></div>
  <input type="text" bind:value={searchInput} placeholder=" ..." />
  <button on:click={searchAddress}></button>
  {#if searchResults.length}
    <ul>
      {#each searchResults as result}
        <li on:click={() => selectAddress(result)}>{result.label}</li>
      {/each}
    </ul>
  {/if}
  {#if isLoading}
    <p>   ....</p>
  {:else if errorMessage}
    <p>{errorMessage}</p>
  {:else if weatherData.length > 0}
    <div>
      <h2> </h2>
      <!--     -->
      <div>
        <p><strong> :</strong></p>
        <p>: {weatherData[0].date}</p>
        <p>: {currentAddress}</p>
        <p>: {weatherData[0].temperature}</p>
        <p>: {weatherData[0].humidity}</p>
        <p>: {weatherData[0].windSpeed}</p>
        <p>: {weatherData[0].clouds}</p>
      </div>
      <!--       -->
      {#if weatherData.length > 1}
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {#each weatherData.slice(1) as data, index}
              <tr>
                <td>{data.date}</td>
                <td>{data.temperature}</td>
                <td>{data.humidity}</td>
                <td>{data.windSpeed}</td>
                <td>{data.clouds}</td>
              </tr>
            {/each}
          </tbody>
        </table>
      {/if}
    </div>
  {/if}
  <div>
    <canvas id="weatherChart"></canvas>
  </div>
</main>

<style>
  main {
    text-align: center;
    padding: 1em;
    margin: 0 auto;
  }

  #map {
    height: 400px;
  }

  table {
    width: 100%;
    border-collapse: collapse;
  }

  th,
  td {
    text-align: left;
    padding: 8px;
    border: 1px solid #ddd;
  }

  th {
    background-color: #f2f2f2;
  }
</style>
